// File: _worker.js
export default {
  async fetch(request, env, ctx) {
    const url = new URL(request.url);
    const pathname = url.pathname;

    console.log(`Worker handling request for: ${pathname}`);

    try {
      // Gérer les fichiers statiques
      if (
        pathname.startsWith('/_next/') ||
        pathname.startsWith('/images/') ||
        pathname.startsWith('/static/') ||
        pathname.endsWith('.js') ||
        pathname.endsWith('.css') ||
        pathname.endsWith('.json') ||
        pathname.endsWith('.ico') ||
        pathname.endsWith('.png') ||
        pathname.endsWith('.jpg') ||
        pathname.endsWith('.svg') ||
        pathname === '/favicon.ico' ||
        pathname === '/robots.txt' ||
        pathname === '/sitemap.xml' ||
        pathname === '/manifest.json'
      ) {
        // Servir le fichier statique directement
        return fetch(request);
      }

      // Gérer les routes API
      if (pathname.startsWith('/api/')) {
        console.log(`Handling API route: ${pathname}`);
        
        // Pour les routes API dynamiques comme /api/hospitals/1
        const hospitalIdMatch = pathname.match(/^\/api\/hospitals\/([^/]+)$/);
        if (hospitalIdMatch) {
          const id = hospitalIdMatch[1];
          console.log(`API hospital ID: ${id}`);
          
          try {
            // Essayer de servir le fichier API spécifique
            const apiResponse = await fetch(new URL(`/api/hospitals/${id}.json`, url.origin));
            if (apiResponse.ok) {
              return new Response(await apiResponse.text(), {
                headers: {
                  'Content-Type': 'application/json',
                  'Access-Control-Allow-Origin': '*'
                }
              });
            }
            
            // Si le fichier spécifique n'existe pas, essayer index.json
            const indexResponse = await fetch(new URL(`/api/hospitals/index.json`, url.origin));
            if (indexResponse.ok) {
              const hospitals = await indexResponse.json();
              const hospital = hospitals.find(h => h.id === id);
              
              if (hospital) {
                return new Response(JSON.stringify(hospital), {
                  headers: {
                    'Content-Type': 'application/json',
                    'Access-Control-Allow-Origin': '*'
                  }
                });
              }
            }
            
            // Si aucune correspondance, retourner une erreur 404
            return new Response(JSON.stringify({ error: 'Hospital not found' }), {
              status: 404,
              headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*'
              }
            });
          } catch (e) {
            console.error(`Error serving API hospital: ${e}`);
            return new Response(JSON.stringify({ error: 'Internal server error' }), {
              status: 500,
              headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*'
              }
            });
          }
        }
        
        // Pour la route API principale /api/hospitals
        if (pathname === '/api/hospitals') {
          console.log('Serving main API hospitals route');
          try {
            const apiResponse = await fetch(new URL('/api/hospitals/index.json', url.origin));
            if (apiResponse.ok) {
              return new Response(await apiResponse.text(), {
                headers: {
                  'Content-Type': 'application/json',
                  'Access-Control-Allow-Origin': '*'
                }
              });
            } else {
              throw new Error('API hospitals not found');
            }
          } catch (e) {
            console.error(`Error serving API hospitals: ${e}`);
            return new Response(JSON.stringify({ error: 'Hospitals data not available' }), {
              status: 404,
              headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*'
              }
            });
          }
        }
        
        // Pour les autres routes API, retourner 404
        return new Response(JSON.stringify({ error: 'API endpoint not found' }), {
          status: 404,
          headers: {
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*'
          }
        });
      }

      // Gérer les routes dynamiques comme /hospitals/1
      const hospitalPageMatch = pathname.match(/^\/hospitals\/([^/]+)$/);
      if (hospitalPageMatch) {
        // Pour les pages d'hôpitaux spécifiques, servir index.html
        try {
          const indexResponse = await fetch(new URL('/index.html', url.origin));
          return new Response(await indexResponse.text(), {
            headers: {
              'Content-Type': 'text/html',
              'Cache-Control': 'public, max-age=0, must-revalidate'
            }
          });
        } catch (e) {
          console.error(`Error serving hospital page: ${e}`);
          return new Response('Page not found', { status: 404 });
        }
      }

      // Pour la page d'accueil et toutes les autres routes, servir index.html
      console.log(`Serving index.html for route: ${pathname}`);
      try {
        const indexResponse = await fetch(new URL('/index.html', url.origin));
        return new Response(await indexResponse.text(), {
          headers: {
            'Content-Type': 'text/html',
            'Cache-Control': 'public, max-age=0, must-revalidate'
          }
        });
      } catch (e) {
        console.error(`Error serving index: ${e}`);
        return new Response('Page not found', { status: 404 });
      }
    } catch (globalError) {
      console.error(`Global worker error: ${globalError}`);
      return new Response(`Server Error: ${globalError.message}`, { status: 500 });
    }
  }
};